import {
  collection,
  getDocs,
  orderBy,
  query
  } from '@firebase/firestore';
import { database } from '../index';

export type AppType = {
  id: string;
  title: string;
  description: string;
  technologies: string[];
  image: string;
  url: string;
};

export class AppDoc {
  static async getAll(): Promise<AppType[] | null> {
    const docRef = collection(database, "apps");
    const appDocs = await getDocs(query(docRef, orderBy("title", "asc")));

    if (!appDocs.empty) {
      const result = appDocs.docs.map((doc) => {
        return {
          id: doc.id,
          title: doc.data().title,
          description: doc.data().description,
          technologies: doc.data().technologies,
          image: doc.data().image,
          url: doc.data().url,
        };
      });
      return result;
    }
    return null;
  }
}
