import { useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import ImgLogo from '../../assets/logo.png';
import { Container } from './style';

export function Menu() {
  // --- Hooks ---
  const { pathname } = useLocation()

  // --- States ---
  const [isMenuMobile, setIsMenuMobile] = useState(false)

  // --- Functions ---
  function isActived(key: string) {
    console.log(pathname);
    if (key === pathname || `/${key}` === pathname) return 'actived';
    return ''
  }

  // --- Renders ---
  return (
    <Container className={isMenuMobile ? 'active' : ''}>
      <img src={ImgLogo} alt="Logo" />

      <Link onClick={() => setIsMenuMobile(false)} className={isActived('/')} to="/">Home</Link>
      <Link onClick={() => setIsMenuMobile(false)} className={isActived('about')} to="/about">About</Link>
      <Link onClick={() => setIsMenuMobile(false)} className={isActived('contact')} to="/contact">Contact</Link>
      <Link onClick={() => setIsMenuMobile(false)} className={isActived('apps')} to="/apps">Apps</Link>

      <button className={isMenuMobile ? 'active' : ''} onClick={() => setIsMenuMobile(!isMenuMobile)}>
        <span className="icon-menu" />
      </button>
    </Container>
  )
}