import { useEffect, useState } from 'react';
import { Loading } from '../../components/Loading';
import { AppDoc, AppType } from '../../services/firebase/Documents/AppDoc';
import { Container } from './style';

export function Apps() {
  // --- States ---
  const [data, setData] = useState<AppType[]>([])
  const [loading, setLoading] = useState(true)

  // --- Effects ---
  useEffect(() => {
    loadData()
  }, [])

  useEffect(() => {
    // !After trigger setData set loading to false
    setLoading(false)
  }, [setData])

  // --- Functions ---
  async function loadData() {
    setLoading(true)
    const result = await AppDoc.getAll() as AppType[]
    setData(result)
  }

  return loading ? (
    <Loading />
  ) : (
    <Container>
      {data ?
        data.map(item => (
          <a key={item.id} href={item.url} target="_blank" rel="noreferrer">
            <img width={250} height={100} src={item.image} alt="Title" />
            <h2>{item.title}</h2>
            <p>{item.description}</p>
            <div className="techs">
              {item.technologies ? item.technologies.map(value => (
                <span key={Math.random()} className={`color-` + String(value).toLowerCase()}>
                  {value}</span>
              )) : null}
            </div>
          </a>
        ))
        : null
      }
    </Container>
  )
}